import apiFetch from "@wordpress/api-fetch"
import { Button, PanelBody, PanelRow } from "@wordpress/components"
import { InnerBlocks, InspectorControls, MediaUpload, MediaUploadCheck } from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"
import { useEffect } from "@wordpress/element" 

registerBlockType("fitnessblocktheme/fitnessslide",{
    title:"Fitness Slides",
    supports:{
        align:["full"]
    },
    attributes:{
        align:{type:"string",default:"full"},
        imgID:{type:"number"},
        imgURL:{type:"string",default:fitnessslide.fallbackImage}
    },
    edit:EditComponent,
    save:SaveComponent
})

function EditComponent(props){
    useEffect(
        function(){
            if(props.attributes.imgID){
                async function go(){
                    const response=await apiFetch({
                        path:`/wp/v2/media/${props.attributes.imgID}`,
                        method:"GET"
                    })
                    props.setAttributes({imgURL:response.media_details.sizes.full.source_url})
                }
                go()
            }
        
    },[props.attributes.imgID])

    function onFileSelect(x){
        props.setAttributes({imgID:x.id})
    }
    return(
        <>
            <InspectorControls>
                <PanelBody title="Slider Image" initialOpen={true}>
                    <MediaUploadCheck>
                        <MediaUpload 
                            onSelect={onFileSelect} 
                            value={props.attributes.imgID} 
                            render={({open})=>{
                                return <Button onClick={open}>Choose Image</Button>
                            }}
                        />
                    </MediaUploadCheck>
                </PanelBody>
            </InspectorControls>
            <div className="row">
                <div className="col-xl-4 col-lg-4 col-12">
                    <img src={`${props.attributes.imgURL}`} alt=""/>
                </div>
                <div className="col-xl-8 col-lg-8 col-12 client-div">
                    <div className="client-text">
                        <InnerBlocks allowedBlocks={['core/paragraph','fitnessblocktheme/fitnessslidetextarea','fitnessblocktheme/fitnessslidename','fitnessblocktheme/fitnessslideheading']}/>
                    </div>
                    <div className="quote-icon">
                        <img src="../wp-content/themes/fitness-block-theme/images/images(1).png" alt=""/>
                    </div> 
                </div>
            </div>
        </>
    )
}


function SaveComponent(){
    return <InnerBlocks.Content/>
}